body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.table__cell {
    align-items: center;
    display: flex;
}

.table__cell-btn {
    margin: 0 10px;
}

.ReactTable .rt-thead .rt-th {
  border: none;
}

.ReactTable .rt-tbody .rt-td {
  border: none;
}

.ReactTable .rt-tbody .rt-tr-group {
  border: none;
  background-color: #F1F8FF;
  margin: 2px 0 2px 0;
  max-height: 40px;
}

.ReactTable .rt-thead.-header {
  box-shadow: none;
  border-bottom: 1px solid #D7D7DB;
  font-size: 18px;
  font-weight: bold;
  color: #737373;
  margin-bottom: 2px;
}
/* .no-data-tbody .ReactTable .rt-thead.-header {
  color: #B1B1B3;
} */

.ReactTable {
  border: none;
  /* padding-bottom: 16px; */
  /* height: 370px; */
}

.ReactTable .rt-tbody .rt-tr-group:hover {
  background-color: #C4DAF7;
  color: white;
  font-weight: bold;
}
.ReactTable .rt-tbody .rt-tr-group:hover button {
  visibility: visible;
}
.ReactTable .rt-tr.selected button {
  visibility: visible;
  color: white;
}
.ReactTable .rt-tr.selected button:hover {
  background: rgb(0,96,223);
}

.ReactTable .rt-thead .rt-th.-sort-desc {
  background-color: rgba(12,12,13,0.1);
  box-shadow: none;
}
.ReactTable .rt-thead .rt-th.-sort-desc span {
  color: black;
}
.ReactTable .rt-thead .rt-th.-sort-asc {
  background-color: rgba(12,12,13,0.1);
  box-shadow: none;
}
.ReactTable .rt-thead .rt-th.-sort-asc span {
  color: black;
}

.ReactTable .rt-td.status-cell {
  padding: 0;
  box-shadow: 4px 0px 0px 0px rgba(255,255,255,1);
}

.no-data-tbody {
  height: 300px;
  font-weight: bold;
  font-size: 28px;
  color: rgba(215,215,219,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #D7D7DB;
  margin-top: 2px;
}

.rt-th.-sort-asc .sort-icons, .rt-th.-sort-desc .sort-icons {
  display: flex;
}

.-sort-asc .sort-up {
  color: #0060DF;
}
.-sort-desc .sort-down {
  color: #0060DF;
}

.ReactTable .rt-tbody {
  overflow: overlay;
}
